.modal-inner {
  display: grid;
  grid-template-columns: repeat(10, 0.5fr);
  grid-template-rows: repeat(10, 0.5fr);
  gap: 1rem;
  padding: 2rem;
  min-height: 500px;
}

.sidebar-text {
  grid-area: 1 / 1 / 11 / 2;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
}

.sidebar-text .vertical-text {
  transform: rotate(-90deg);
  white-space: nowrap;
  font-size: var(--font-32);
  font-family: var(--font-oliver);
  color: var(--color-black);
}

.main-content {
  grid-area: 1 / 2 / 11 / 11;
  padding: 2rem;
  border-radius: 25px;
  overflow-y: auto;
}

.main-titled {
  font-family: var(--font-oliver);
  font-size: var(--font-24);
  margin-bottom: 1rem;
  margin-top: 1.5rem;
}

.main-titled:first-child {
  margin-top: 0;
}

.main-content p {
  font-family: var(--font-catamaran);
  font-size: var(--font-16);
  margin: 0.5rem 0;
  line-height: 1.6;
}

.main-content a {
  color: var(--color-black);
  text-decoration: underline;
  transition: opacity 0.2s;
}

.main-content a:hover {
  opacity: 0.7;
}

/* Specific sidebar colors */
.sidebar-work {
  background-color: var(--color-yellow);
}

.sidebar-education {
  background-color: var(--color-white);
}

.sidebar-contact {
  background-color: var(--color-green);
}

/* Specific main content colors */
.main-work {
  background-color: var(--color-blue);
}

.main-education {
  background-color: var(--color-green);
}

.main-contact {
  background-color: var(--color-blue);
}

@media (max-width: 768px) {
  .modal-inner {
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    padding: 1.5rem;
  }

  .sidebar-text {
    grid-area: auto;
    padding: 1rem;
  }

  .sidebar-text .vertical-text {
    transform: rotate(0deg);
    font-size: var(--font-24);
  }

  .main-content {
    grid-area: auto;
    padding: 1.5rem;
  }
}
