.about-modal {
  display: grid;
  grid-template-columns: repeat(10, 0.5fr);
  grid-template-rows: repeat(10, 0.5fr);
  gap: 1rem;
  padding: 2rem;
  min-height: 600px;
}

.about-modal .sidebar-text {
  background-color: var(--color-blue);
  grid-area: 1 / 1 / 11 / 2;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
}

.about-modal .sidebar-text .vertical-text {
  transform: rotate(-90deg);
  white-space: nowrap;
  font-size: var(--font-32);
  font-family: var(--font-oliver);
  color: var(--color-black);
}

.about-content {
  grid-area: 1 / 2 / 11 / 11;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(9, 1fr);
  gap: 1rem;
}

.about-section:first-child .about-text {
  background-color: var(--color-white);
  font-family: var(--font-catamaran);
  padding: 2rem;
  grid-area: 1 / 1 / 6 / 7;
  border-radius: 25px;
  font-size: var(--font-16);
  line-height: 1.6;
  display: flex;
  align-items: center;
}

.about-section:first-child .about-photo {
  grid-area: 1 / 7 / 6 / 10;
}

.about-section:last-child .about-text {
  background-color: var(--color-green);
  font-family: var(--font-catamaran);
  padding: 2rem;
  grid-area: 6 / 4 / 10 / 10;
  border-radius: 25px;
  font-size: var(--font-16);
  line-height: 1.6;
  display: flex;
  align-items: center;
}

.about-section:last-child .about-photo {
  grid-area: 6 / 1 / 10 / 4;
}

.about-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 25px;
}

@media (max-width: 768px) {
  .about-modal {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    padding: 1.5rem;
  }

  .about-modal .sidebar-text {
    grid-area: auto;
    padding: 1rem;
  }

  .about-modal .sidebar-text .vertical-text {
    transform: rotate(0deg);
    font-size: var(--font-24);
  }

  .about-content {
    grid-area: auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .about-section:first-child .about-text,
  .about-section:last-child .about-text,
  .about-section:first-child .about-photo,
  .about-section:last-child .about-photo {
    grid-area: auto;
  }

  .about-photo {
    max-height: 300px;
  }
}
